.left {
    float: left;
}

.right {
    float: right;
}

.cf {
    zoom: 1;
}

.cf:after {
    clear: both;
    content: ' ';
    display: block;
    height: 0;
    overflow: hidden;
    visibility: hidden;
}

ul {
    list-style-type: disc;
    list-style-position: outside;
}

.bullets-inside {
    list-style-position: inside;
}

.image-right {
    float: right;
    margin: 0 10px 10px 10px;
}

.bold,
b {
    font-weight: bold;
}

i {
    font-style: italic;
}

