// reset styles

.left {
    float: left;
}
.right {
    float: right;
}

//an alternate clearfix method
.cf {
    zoom: 1;
}
.cf:after {
    clear: both;
    content: ' ';
    display: block;
    height: 0;
    overflow: hidden;
    visibility: hidden;
}
ul {
    list-style-type: disc;
    list-style-position: outside;
}
.bullets-inside {
    list-style-position: inside;
}
.image-right {
    float: right;
    margin: 0 10px 10px 10px;
}
.bold,
b {
    font-weight: bold;
}
i {
    font-style: italic;
}
/*FOUC me*/

.js #promo-area {
    @for $i from 1 through 10 {
        #slide-#{$i} {
            display: none;
        }
    }
    #slide-1 {
        display: block;
    }
}
